.fg-bg {
    position: relative;
}

.fg-bg:before {
    background-image: url("/src/assets/img/fg-bg.jpg");
    opacity: 0.5;
    content: " ";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: 50% 12%;
    background-size: cover;
}

/* img {
    max-width: 100px;
} */

.signature-container {
    margin-right: 0;
    margin-left: 0;
    border-color: hsl(0, 0%, 80%);
    border-radius: 4px;
    border-style: solid;
    border-width: 1px;
    height: 300 !important;
    width: 330 !important;
}

.signature-container-disabled {
    margin-right: 0;
    margin-left: 0;
    border-color: hsl(0, 0%, 80%);
    border-radius: 4px;
    border-style: solid;
    border-width: 1px;
    background-color: #e2e8ee;
}

.table-action {
    cursor: pointer;
}
.table-action svg {
    color: #6c757d;
}

.table-action svg:hover {
    color: #212529;
}

.custom-tab.tab-vertical .nav-tabs {
    margin-right: 10px;
    background: white;
}

.custom-tab.tab-vertical .nav-tabs .nav-link.active {
    background: #3f80ea;
    color: white;
}

.custom-tab.tab-vertical .nav-item {
    border-bottom: 1px solid rgba(0, 0, 0, 0.125);
}

.custom-tab.tab-vertical .nav-link:hover:not(.active) {
    color: #495057;
    text-decoration: none;
    background-color: #f4f7f9;
}

.fault-image-holder {
    margin: 8px;
    border: 1px solid #ddd;
    box-shadow: 1px 1px 5px 0 #a2958a;
    padding: 6px;
    float: left;
    display: inline-block;
    width: 140px;
    overflow: hidden;
    position: relative;
}

.fault-image-holder img {
    max-width: none;
    width: 100%;
    height: -webkit-fill-available;
}

.fault-image-holder small {
    font-size: 70%;
    display: block;
    text-align: center;
    margin-top: 10px;
}

.fault-image-holder .close {
    cursor: pointer;
    position: absolute;
    top: 10px;
    right: 10px;
    font-weight: 900;
}
.file-uploader-modal img {
    max-width: none;
    width: 100%;
}

.table-layout-fixed {
    table-layout: fixed;
}

.cursor-not-allowed {
    cursor: not-allowed;
    pointer-events: none;
}
